import React, { useState, useEffect } from "react";
import Chart from "chart.js/auto";

const Analytics = ({ token, urlId }) => {
  const [stats, setStats] = useState([]);
  const [message, setMessage] = useState("");
  const [filter, setFilter] = useState("daily"); // : daily, hourly, etc.

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const response = await fetch(`http://localhost:8000/api/urls/${urlId}/stats`, {
          method: "GET",
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          setStats(data);
        } else {
          setMessage("Failed to fetch analytics data.");
        }
      } catch (error) {
        setMessage("Error fetching analytics data.");
      }
    };

    fetchStats();
  }, [token, urlId]);

  useEffect(() => {
    if (stats.length > 0) {
      const canvas = document.getElementById("analyticsChart");
      if (!canvas) return;

      const ctx = canvas.getContext("2d");
      if (window.analyticsChart) window.analyticsChart.destroy(); 

      window.analyticsChart = new Chart(ctx, {
        type: "line",
        data: {
          labels: stats.map((entry) => entry.timestamp),
          datasets: [
            {
              label: "Clicks",
              data: stats.map((entry) => entry.clicks),
              borderColor: "blue",
              fill: false,
            },
          ],
        },
        options: {
          scales: {
            x: { title: { display: true, text: "" } },
            y: { title: { display: true, text: "" } },
          },
        },
      });
    }
  }, [stats]);


  return (
    <div>
      <h2>Analytics</h2>
      <select value={filter} onChange={(e) => setFilter(e.target.value)}>
        <option value="daily">Daily</option>
        <option value="hourly">Hourly</option>
        <option value="minute">Minute</option>
      </select>
      {message && <p>{message}</p>}
      <canvas id="analyticsChart"></canvas>
    </div>
  );
};

export default Analytics;
